export default {
    data() {
        return {
            imgLists: [],
            canAdd: true
        }
    },
    props: {
        imgTitle: {
            type: String,
            default: ''
        },
        maxNum: {
            type: Number,
            default: 1
        },
        button: {
            type: String,
            default: '照片'
        }
    },
    methods: {
        addImg() {
            var num = this.maxNum - this.imgLists.length;
            let that = this;
            if (num < 1) {
                return false;
            }
            uni.showLoading({
                title: ""
            });
            uni.chooseImage({
                count: num,
                sizeType: ['compressed'],
                success: function(res) {
                    that.imgLists = that.imgLists.concat(res.tempFilePaths);
                    if (that.imgLists.length >= that.maxNum) {
                        that.canAdd = false;
                    }
                    uni.hideLoading();
                    this.$emit('getImages', this.imgLists)
                },
                fail: function() {
                    uni.hideLoading();
                }
            });
        },
        removeImg: function(index) {
            this.imgLists.splice(index, 1);
            this.imgLists = this.imgLists;
            if (this.imgLists.length < this.maxNum) {
                this.canAdd = true;
            }
            this.$emit('getImages', this.imgLists)
        },
        showImgs: function(e) {
            var currentImg = e.currentTarget.dataset.imgurl;
            let that = this;
            uni.previewImage({
                urls: that.imgLists,
                current: currentImg
            })
        }
    }
}
